@charset "UTF-8";
/**
    リセットと初期設定を両方記載
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");
*,
::before,
::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, body {
  padding: 0;
  margin: 0;
  font-size: 62.5%;
}

body {
  color: #333;
  font-size: 16px;
  font-family: "Noto Sans JP", sans-serif;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6,
ul, li, ol, small, b, a,
strong, dl, dt, dd {
  padding: 0;
  margin: 0;
}

ul, ol {
  list-style: none;
}

.section__title {
  font-weight: bold;
  font-size: 4.8rem;
  text-align: center;
  margin: 10px 0;
}

.section__area {
  padding: 100px 0;
}

.inner {
  width: 1280px;
  max-width: 100%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.header__logo {
  width: 200px;
  margin: auto 0;
}

.header__logo img {
  width: 100%;
}

.header .header__btn {
  width: 80px;
  height: 80px;
  top: 30px;
  right: 30px;
  transition: 0.6s;
  position: relative;
  position: fixed;
  z-index: 300;
}

.header .header__btn span {
  position: absolute;
  width: 100%;
  background-color: #000;
  height: 2px;
  transition: ease 0.4s;
  display: block;
}

.header .header__btn span:nth-of-type(1) {
  top: 0;
}

.header .header__btn span:nth-of-type(2) {
  top: 50%;
}

.header .header__btn span:nth-of-type(3) {
  bottom: 0;
}

.header .header__btn.active {
  left: 0;
}

.header .header__btn.active > span:nth-of-type(1) {
  top: 50%;
  transform: rotate(45deg);
}

.header .header__btn.active > span:nth-of-type(2) {
  display: none;
}

.header .header__btn.active > span:nth-of-type(3) {
  top: 50%;
  transform: rotate(-45deg);
}

.header__nav {
  display: none;
  position: fixed;
  top: 0;
  padding: 0;
  width: 100%;
  z-index: 200;
}

.header__nav.active {
  display: block;
}

.header__nav ul li {
  text-align: center;
}

.header__nav ul li a {
  color: #333;
  font-size: 1.6rem;
  padding: 30px 0;
  display: block;
  transition: 0.3s;
}

.header__nav ul li a:hover {
  text-decoration: underline;
  background: red;
  color: #fff;
}

.header__bg.active {
  z-index: 100;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(233, 233, 233, 0.611);
}

.top {
  margin-top: 50px;
  position: relative;
}

.top img {
  height: 100vh;
}

.top__text {
  font-size: 4.8rem;
}

.top__text-up {
  position: absolute;
  top: 30%;
  left: 20%;
}

.top__text-down {
  position: absolute;
  bottom: 10%;
  left: 20%;
}